@media (min-width: $(desktop)) {
    .section.tabbed-product-listings {
        padding: 60px 0 0;
    }
}
.section.tabbed-product-listings .tabs,
    .section.tabbed-product-listings .tab-bodies,
    .section.tabbed-product-listings .tab-body {
    width: 100%;
    position: relative;
    height: auto !important;
}
.section.tabbed-product-listings .tab-body > .title {
    position: relative;
    background: $purple;
    margin: 0;
    padding: 30px 0;
}
.section.tabbed-product-listings .tab-body > .title::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26px;
    border-width: 26px 26px 0 26px;
    border-color: $purple transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
@media (min-width: $(desktop)) {
    .section.tabbed-product-listings .tab-body > .title {
        display: none;
    }
}
.section.tabbed-product-listings .tab-body > .title h3 {
    color: $white;
    margin: 0;
    padding: 0;
}
.section.tabbed-product-listings .container {
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
                -ms-grid-row-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.section.tabbed-product-listings .type {
    padding-top: 40px;
}
@media (min-width: $(desktop)) {
    .section.tabbed-product-listings .type {
        padding: 60px 0 20px;
    }
}
.section.tabbed-product-listings .tab-links {
    border-bottom: solid 4px $purple;
    margin: 0;
}
.section.tabbed-product-listings .tab-links .tab {
    background: #d4d4d4;
    border: 0;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: -0.4px;
    text-align: center;
}
.section.tabbed-product-listings .tab-links .tab a {
    color: $black;
    text-transform: capitalize;
}
.section.tabbed-product-listings .tab-links .tab:hover {
    background: $gray_dark1;
}
.section.tabbed-product-listings .tab-links .tab:hover a {
    color: $white;
}
.section.tabbed-product-listings .tab-links .tab.active:hover {
    background: $purple_light;
}
.section.tabbed-product-listings .tab-links .tab.active {
    background: $purple;
}
.section.tabbed-product-listings .tab-links .tab.active a {
    color: $white;
}
.section.tabbed-product-listings h3 {
    padding: 0 30px;
    text-align: center;
    margin-bottom: 30px;
}
.section.tabbed-product-listings .product-listing {
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 30px;
}
.section.tabbed-product-listings .product-listing .product-item {
    margin-bottom: 30px;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 1/1 - (30px - 30px * 1/1));
}
.section.tabbed-product-listings .product-listing .product-item:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.section.tabbed-product-listings .product-listing .product-item:last-child {
    margin-right: 0;
}
.section.tabbed-product-listings .product-listing .product-item:nth-child(1 '' ''n) {
    margin-right: 0;
    margin-left: auto;
}
