.section.tabbed-product-listings .product-item {
    font-family: 'Open Sans';
}
.section.tabbed-product-listings .product-item a {
    text-decoration: none;
}
.section.tabbed-product-listings .product-item .image {
    width: 100%;
    position: relative;
}
.section.tabbed-product-listings .product-item .image img {
    width: 100%;
    height: 100%;
    display: block;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center center;
       object-position: center center;
}
.section.tabbed-product-listings .product-item .image .overlay {
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 43%, rgba(0, 0, 0, 0.74) 100%);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 43%, rgba(0, 0, 0, 0.74) 100%);
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.section.tabbed-product-listings .product-item .image .title {
    position: absolute;
    bottom: 5px;
    width: 100%;
    text-align: center;
}
.section.tabbed-product-listings .product-item .stats {
    color: $white;
    padding: 10px;
}
.section.tabbed-product-listings .product-item .stats ul {
    display: -webkit-box;
